<template>
  <div>
    <el-form inline :model="coverForm" ref="coverForm" :rules="coverRules" :label-position="'right'">
      <el-form-item label="ID" prop="userId">
        <el-input v-model="coverForm.userId" clearable></el-input>
      </el-form-item>
      <el-form-item label="" style="display: block">
        <el-upload action="https://httpbin.org/post" :multiple="false" list-type="picture-card" :limit="1" :before-upload="beforeUpload" :on-exceed="handleExceed" :on-success="handleSuccess" :on-progress="handleProgress" :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
          <div slot="tip" class="el-upload__tip">2M</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" class="imageInfo" required>
        <el-select v-model="country" clearable>
          <el-option v-for="item in countryMap" :label="item.name" :value="item.key" :key="item.key"></el-option>
        </el-select>
        <el-form-item prop="city">
          <el-input v-model="coverForm.city" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item prop="site">
          <el-input v-model="coverForm.site" placeholder="" clearable></el-input>
        </el-form-item>
      </el-form-item>
      <el-form-item label="" style="display: block"><h3></h3></el-form-item>
      <el-form-item style="display: block;width: 400px;text-align: center">
        <el-button @click="resetForm"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    name: 'discoverAdd',
    data () {
      return {
        country: '',
        countryMap: [
          {key: 'cn', name: ''},
          {key: 'us', name: ''},
          {key: 'nsl', name: ''}
        ],
        coverForm: {
          userId: '',
          city: '',
          site: ''
        },
        coverRules: {
          userId: [ {required: true, trigger: 'blur', message: 'ID'} ],
          city: [ {required: true, trigger: 'blur', message: ''} ],
          site: [ {required: true, trigger: 'blur', message: ''} ]
        },
        imageSuccess: false
      }
    },
    methods: {
      submitForm () {
        this.$refs.coverForm.validate(valid => {
          if (valid && this.imageSuccess) {
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          } else {
            this.$message.warning('')
          }
        })
      },
      resetForm () {
        this.$refs.coverForm.resetFields()
      },
      beforeUpload (file) {
        const isLimitSize = file.size / 1024 / 1024 > 2
        this.imageSuccess = false
        if (isLimitSize) {
          this.$message.error('2M,2M')
        }
      },
      handleSuccess () { this.imageSuccess = true },
      handleRemove () { this.imageSuccess = false },
      handleExceed (files) {
        this.$message.warning('1')
      },
      handleProgress (event, file, fileList) {
        console.log(event)
        console.log(file)
        console.log(fileList)
      }
    }
  }
</script>
<style lang="less" scope>
  .el-form--inline {
    width: 500px;
    margin: auto;
    .imageInfo {
      .el-input {
        width: 300px;
        margin: 15px 0 0;
        display: block;
      }
      .el-form-item__content .el-form-item { margin-top: 15px; }
    }
  }
</style>



