<template>
  <div class="edit-main-task">
    <el-form inline label-position="right" label-width="120px" :model="taskForm" ref="taskForm" :rules="taskRules">
      <el-form-item label="" prop="taskName">
        <el-input v-model="taskForm.taskName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="taskDescription">
        <el-input v-model="taskForm.taskDescription"></el-input>
      </el-form-item>
      <el-form-item label="" prop="top">
        <el-radio-group v-model="taskForm.top">
          <el-radio label="yes"></el-radio>
          <el-radio label="no"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="taskCircle">
        <el-radio-group v-model="taskForm.taskCircle">
          <el-radio label="noLimit"></el-radio>
          <el-radio label="limit">&nbsp;&nbsp;<el-date-picker type="datetimerange" range-separator="" start-placeholder="" end-placeholder="" v-model="taskForm.datetimeMerge"></el-date-picker></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-upload action="https://httpbin.org/post" list-type="picture-card"><i class="el-icon-plus"></i></el-upload>
      </el-form-item>
      <el-form-item label="">
        <el-upload action="https://httpbin.org/post" list-type="picture-card"><i class="el-icon-plus"></i></el-upload>
      </el-form-item>
      <el-form-item label="" prop="showRegion">
        <el-radio-group v-model="taskForm.showRegion">
          <el-radio label="all"></el-radio>
          <el-radio label="China"></el-radio>
          <el-radio label="abroad"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="detail">
        <tinymce :height="300" v-model="taskForm.detail"></tinymce>
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button @click="resetForm"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import tinymce from 'views/tinymce/tinymce'
  import { addTask, editTask } from 'api/operationManage'
  import { formatTime } from 'utils/index'
  export default {
    data () {
      // let taskForm
      /* const validateTaskCircle = function (rule, value, cb) {
        if (!value) {
          cb(new Error(''))
          return
        }
        if (value === '1' && ((taskForm.datetime && taskForm.datetime.length === 0) || taskForm.datetime === null)) { /!* nullclearable taskForm.datetime *!/
          cb(new Error(''))
        }
      } */
      return {
        taskForm: {
          taskId: (Math.random() * 10000000).toFixed(0),
          taskName: '',
          taskDescription: '',
          top: 'yes',
          taskCircle: '',
          datetimeMerge: [],
          showRegion: '',
          detail: '',
          createTime: '',
          updateTime: '',
          status: 'effective',
          subTask: [],
          userNumberRecord: []
        },
        taskRules: {
          taskName: [{required: true, trigger: 'blur', message: ''}],
          taskDescription: [{required: true, trigger: 'blur', message: ''}],
          taskCircle: [{required: true, trigger: 'change', message: ''}],
          showRegion: [{required: true, trigger: 'change', message: ''}],
          detail: [{required: true, trigger: 'blur', message: ''}]
        }
      }
    },
    created () { this.editDetail() },
    methods: {
      editDetail () {
        if (this.$route.params.taskId) {
          editTask(this.$route.params.taskId).then(response => {
            this.taskForm = response.data.filteredTask
            this.taskForm.updateTime = formatTime()
          })
        }
      },
      submitForm () {
        this.$refs.taskForm.validate(valid => {
          if (valid) {
            if (this.$route.params.taskId) {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            } else {
              this.taskForm.createTime = formatTime()
              addTask(this.taskForm).then(() => {
                if (this.taskForm.taskCircle === '1' && (this.taskForm.datetimeMerge === null || this.taskForm.datetimeMerge.length === 0)) {
                  this.$message.warning('')
                  return
                }
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success'
                })
              })
            }
          }
        })
      },
      resetForm () {
        this.taskForm = {
          taskId: (Math.random() * 10000000).toFixed(0),
          taskName: '',
          taskDescription: '',
          top: '0',
          taskCircle: '',
          datetimeMerge: [],
          showRegion: '',
          detail: '',
          createTime: '',
          updateTime: '',
          status: 'effective'
        }
        this.$refs.taskForm.clearValidate()
      }
    },
    components: { tinymce }
  }
</script>
<style lang="less">
.edit-main-task .el-form--inline {
  width: 800px;
  margin: auto;
  .el-form-item {
    width: 100%;
    &__content {
       width: 85%;
    }
  }
}
</style>



