<template>
  <div>
    <el-header>
      <div class="filter-item" style="text-align: right">
        <el-button type="primary" @click="handleCreate"><i class="el-icon-circle-plus"></i></el-button>
      </div>
    </el-header>
    <el-main>
      <el-table :data="list" border style="text-align: center">
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.systemType | systemTypeFilter }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="systemName"></el-table-column>
        <el-table-column label=''>
          <template slot-scope="scope">
            <span>{{ scope.row.applicationType | applicationTypeFilter }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="version"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.forceUpdate | forceUpdateFilter }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="downloadURL" min-width="300" align="left"></el-table-column>
        <el-table-column label="" prop="createTime"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="editVersionInfo(scope.row)"><i class="el-icon-edit"></i></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <pagination :total="total" :listQuery="listQuery" :GetList="GetVersionList"></pagination>
    </el-footer>
    <el-dialog title="/" :visible.sync="visible" class="versionDialog">
      <el-form :model="versionForm" ref="versionForm" :rules="versionRules" inline>
        <el-form-item label="" prop="systemType">
          <el-radio-group v-model="versionForm.systemType">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="applicationType">
          <el-radio-group v-model="versionForm.applicationType">
            <el-radio label="0">Android</el-radio>
            <el-radio label="1">IOS</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="downloadURL">
          <el-input v-model="versionForm.downloadURL" clearable style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="systemName">
          <el-input v-model="versionForm.systemName" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="version" style="display: block">
          <el-input v-model="versionForm.version" clearable></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="versionForm.createTime"
            type="datetime"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="forceUpdate">
          <el-radio-group v-model="versionForm.forceUpdate">
            <el-radio label="0"></el-radio>
            <el-radio label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button v-if="isEdit" @click="cancelForm"></el-button>
        <el-button v-else @click="visible = false"></el-button>
        <el-button type="info" @click="resetForm"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { getVersionList } from 'api/system'
  import { formatTime } from 'utils/index'
  export default {
    name: 'appManage',
    data () {
      return {
        list: null,
        total: 1,
        listQuery: {page: 1},
        visible: false,
        versionForm: {
          id: null,
          systemType: '',
          applicationType: '',
          downloadURL: '',
          systemName: '',
          version: '',
          forceUpdate: '',
          createTime: ''
        },
        versionRules: {
          systemType: [{required: true, trigger: 'blur', message: ''}],
          applicationType: [{required: true, trigger: 'blur', message: ''}],
          downloadURL: [{required: true, trigger: 'blur', message: ''}],
          systemName: [{required: true, trigger: 'blur', message: ''}],
          version: [{required: true, trigger: 'blur', message: ''}],
          forceUpdate: [{required: true, trigger: 'blur', message: ''}]
        },
        isEdit: false
      }
    },
    created () { this.GetVersionList() },
    methods: {
      GetVersionList () {
        getVersionList(this.listQuery).then(response => {
          this.list = response.data.versionList
          this.total = response.data.total
        })
      },
      submitForm () {
        this.$refs.versionForm.validate(valid => {
          if (valid) {
            if (this.isEdit) {
              this.list.forEach((item, index) => {
                if (item.id === this.versionForm.id) {
                  this.versionForm.createTime = formatTime(this.versionForm.createTime)
                  this.list.splice(index, 1, this.versionForm)
                  this.$notify({
                    title: '',
                    message: '',
                    type: 'success',
                    duration: 2000
                  })
                }
              })
            } else {
              this.versionForm.createTime = formatTime(this.versionForm.createTime)
              this.list.unshift(this.versionForm)
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration: 2000
              })
            }
            this.visible = false
            this.isEdit = false
          }
        })
      },
      handleCreate () {
        this.visible = true
        this.resetForm()
        this.$nextTick(() => {
          this.$refs.versionForm.clearValidate()
        })
      },
      resetForm () {
        this.versionForm = {
          id: null,
          systemType: '',
          applicationType: '',
          downloadURL: '',
          systemName: '',
          version: '',
          forceUpdate: '',
          createTime: ''
        }
      },
      cancelForm () {
        this.resetForm()
        this.visible = false
        this.isEdit = false
      },
      editVersionInfo (row) {
        this.visible = true
        this.isEdit = true
        this.versionForm = {
          id: row.id,
          systemType: row.systemType + '',
          applicationType: row.applicationType + '',
          downloadURL: row.downloadURL,
          systemName: row.systemName,
          version: row.version,
          forceUpdate: row.forceUpdate + '',
          createTime: row.createTime
        }
      }
    },
    filters: {
      systemTypeFilter (val) {
        const systemTypeMap = ['', '']
        return systemTypeMap[val]
      },
      applicationTypeFilter (val) {
        const applicationTypeMap = ['Android', 'IOS']
        return applicationTypeMap[val]
      },
      forceUpdateFilter (val) {
        const forceUpdateMap = ['', '']
        return forceUpdateMap[val]
      }
    }
  }
</script>
<style lang="less">
.versionDialog {
  .el-dialog__footer {
    text-align: center;
  }
  .el-form{
    width: 500px;
  }
}
</style>



