<template>
  <div>
    <certificate-filter v-on:sendListQuery="acceptQuery" :GetList="GetCoverList">
      <div class="filter-item">
        <el-input v-model="listQuery.userId" placeholder="ID" clearable></el-input>
      </div>
    </certificate-filter>
    <el-main>
      <el-table :data="list" border style="text-align: center">
        <el-table-column label="" prop="id"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img :src="scope.row.image" alt="">
          </template>
        </el-table-column>
        <el-table-column label="" prop="position"></el-table-column>
        <el-table-column label="ID" prop="userId"></el-table-column>
        <el-table-column label="" prop="uploadTime"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status | statusTagFilter">{{ scope.row.status | statusFilter }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="220px">
          <template slot-scope="scope">
            <template v-if="scope.row.status === 1">
              <el-button type="success" @click="pass(scope.row)"></el-button>
              <el-button type="danger" @click="noPass(scope.row)"></el-button>
              <el-button type="warning" @click="deleteCover(scope.row)"></el-button>
            </template>
            <template v-else>
              <el-button type="primary" @click="review(scope.row)"></el-button>
              <el-button type="warning" @click="deleteCover(scope.row)"></el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <pagination :total="total" :listQuery="listQuery" :GetList="GetCoverList"></pagination>
    </el-footer>
  </div>
</template>
<script>
  import { getCoverList } from 'api/contentManagement'
  export default {
    name: 'coverPending',
    props: {
      type: {
        type: Number,
        default: 1
      }
    },
    data () {
      return {
        activeName: 'pending',
        list: null,
        total: 1,
        listQuery: {page: 1, type: this.type, userId: null}
      }
    },
    created () { this.GetCoverList() },
    methods: {
      GetCoverList () {
        getCoverList(this.listQuery).then(response => {
          this.list = response.data.coverList
          this.total = response.data.total
        })
      },
      review (row) {
        for (const [i, v] of this.list.entries()) {
          if (v.userId === row.userId) {
            this.list[i].status = 1
            break
          }
        }
        this.$notify({
          title: 'Prompt',
          message: '',
          type: 'info',
          duration: 2000
        })
      },
      pass (row) {
        for (const [i, v] of this.list.entries()) {
          if (v.userId === row.userId) {
            this.list[i].status = 2
            break
          }
        }
        this.$notify({
          title: 'Prompt',
          message: '',
          type: 'info',
          duration: 2000
        })
      },
      noPass (row) {
        for (const [i, v] of this.list.entries()) {
          if (v.userId === row.userId) {
            this.list[i].status = 0
            break
          }
        }
        this.$notify({
          title: 'Prompt',
          message: '',
          type: 'info',
          duration: 2000
        })
      },
      deleteCover (row) {
        for (const [i, v] of this.list.entries()) {
          if (v.userId === row.userId) {
            this.list.splice(i, 1)
            break
          }
        }
        this.$notify({
          title: 'Prompt',
          message: '',
          type: 'info',
          duration: 2000
        })
      },
      acceptQuery (query) {
        this.listQuery = query.listQuery
        this.listQuery.type = this.type
      }
    },
    filters: {
      statusFilter (val) {
        const status = ['', '', '']
        return status[val]
      },
      statusTagFilter (val) {
        const status = ['danger', 'primary', 'success']
        return status[val]
      }
    }
  }
</script>
<style lang="less">

</style>



