<template>
  <div>
    <certificate-filter v-on:sendListQuery="acceptQuery" :GetList="GetBannerList">
      <div class="filter-item">
        <el-input v-model="listQuery.title" placeholder="" clearable></el-input>
      </div>
      <div class="filter-item fr">
        <el-button type="primary" @click="addBanner"></el-button>
      </div>
    </certificate-filter>
    <el-main>
      <el-table :data="list" border style="text-align: center" v-loading="loading" element-loading-text=",...">
        <el-table-column label="" prop="title" align="left" width="300px"></el-table-column>
        <el-table-column label="banner">
          <template slot-scope="scope">
            <img :src="scope.row.banner[0].url" width="100" height="65" alt="">
          </template>
        </el-table-column>
        <el-table-column label="url" prop="url" align="left" min-width="260px"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.type | typeFilter }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="createTime"></el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-button :type="scope.row.effective | effectiveTagFilter" @click="changeStatus(scope.row)">{{ scope.row.effective | effectiveFilter }}</el-button>
            <el-button type="primary" @click="editBanner(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <pagination :total="total" :listQuery="listQuery" :GetList="GetBannerList"></pagination>
    </el-footer>
    <el-dialog title="/banner" :visible.sync="visible" center class="banner-dialog">
      <el-form :model="bannerForm" ref="bannerForm" :rules="bannerRules" label-width="140px" position-label="right">
        <el-form-item label="" prop="type">
          <el-radio-group v-model="bannerForm.type">
            <el-radio label="user"></el-radio>
            <el-radio label="pro"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="url" prop="url">
          <el-input v-model="bannerForm.url" clearable></el-input>
        </el-form-item>
        <el-form-item label="url" prop="title">
          <el-input v-model="bannerForm.title" clearable></el-input>
        </el-form-item>
        <el-form-item label="banner" prop="banner">
          <el-upload action="https://httpbin.org/post" list-type="picture-card" :file-list="bannerForm.banner" :on-success="handleSuccess" :on-remove="handleRemove" :limit="1" :on-exceed="handleExceed"><i class="el-icon-plus"></i></el-upload>
        </el-form-item>
        <el-form-item label="" prop="effective">
          <el-radio-group v-model="bannerForm.effective">
            <el-radio label="effective"></el-radio>
            <el-radio label="ineffective"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="visible = false"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { getBannerList, editBanner } from 'api/operationManage'
  import { formatTimeCustom } from 'utils/index'
  export default {
    name: 'adManage',
    data () {
      return {
        list: null,
        total: 1,
        listQuery: {page: 1, title: null},
        loading: true,
        visible: false,
        isEdit: false,
        bannerForm: {
          id: '',
          type: '',
          url: '',
          title: '',
          banner: [],
          effective: ''
        },
        bannerRules: {
          type: [{required: true, trigger: 'blur', message: ''}],
          url: [{required: true, trigger: 'blur', message: 'banner'}, { type: 'url', message: '', trigger: 'blur,change' }],
          title: [{required: true, trigger: 'blur', message: 'banner'}],
          banner: [{required: true, trigger: 'blur', message: 'banner'}],
          effective: [{required: true, trigger: 'blur', message: ''}]
        }
      }
    },
    created () { this.GetBannerList() },
    methods: {
      GetBannerList () {
        this.loading = true
        getBannerList(this.listQuery).then(response => {
          this.list = response.data.bannerList
          this.total = response.data.total
          this.loading = false
        })
      },
      changeStatus (row) {
        row.effective = row.effective === 'effective' ? 'ineffective' : 'effective'
        this.$notify({
          title: 'Prompt',
          message: 'banner',
          type: 'success',
          duration: 2000
        })
      },
      addBanner () {
        this.isEdit = false
        this.visible = true
        this.bannerForm = {
          id: '',
          type: '',
          url: '',
          title: '',
          banner: [],
          effective: '',
          createTime: ''
        }
        this.$nextTick(() => {
          this.$refs.bannerForm.clearValidate()
        })
      },
      editBanner (row) {
        this.isEdit = true
        this.visible = true
        this.bannerForm = row
      },
      submitForm () {
        this.$refs.bannerForm.validate(valid => {
          if (this.bannerForm.banner.length === 0) this.$message.warning('')
          if (valid) {
            editBanner(this.bannerForm).then(response => {
              if (this.isEdit) {
                for (let item of this.list) {
                  if (item.id === this.bannerForm.id) {
                    item = this.bannerForm
                    this.$notify({
                      title: 'Prompt',
                      message: 'banner',
                      type: 'success'
                    })
                    break
                  }
                }
              } else {
                this.bannerForm.createTime = formatTimeCustom(new Date(), '{y}-{m}-{d}')
                this.list.unshift(this.bannerForm)
                this.$notify({
                  title: 'Prompt',
                  message: 'banner',
                  type: 'success'
                })
              }
              this.visible = false
            })
          }
        })
      },
      handleSuccess (response, file) {
        this.bannerForm.banner = [] // deleteundefined,(),
        this.bannerForm.banner.push({name: file.name, url: file.url})
      },
      handleRemove (file) {
        if (this.bannerForm.banner.length !== 0) {
          this.bannerForm.banner.forEach((item, index) => {
            if (item.url === file.url) {
              delete this.bannerForm.banner[0]
            }
          })
        }
      },
      handleExceed (files, fileList) {
        this.$message.warning('')
      },
      acceptQuery (query) {
        this.listQuery = query.listQuery
      }
    },
    filters: {
      typeFilter (type) {
        const typeObj = {user: '', pro: ''}
        return typeObj[type]
      },
      effectiveFilter (effective) {
        const effectiveObj = {effective: '', ineffective: ''}
        return effectiveObj[effective]
      },
      effectiveTagFilter (effective) {
        const effectiveTagObj = {effective: 'success', ineffective: 'danger'}
        return effectiveTagObj[effective]
      }
    }
  }
</script>
<style lang="less">

</style>



