<template>
  <div class="production-container">
    <certificate-filter v-on:sendListQuery="acceptQuery" :GetList="GetRecommendProduction">
      <div class="filter-item">
        <el-input v-model="listQuery.productionName" placeholder="" clearable></el-input>
      </div>
      <div class="filter-item fr">
        <el-button type="primary" @click="addProduction"></el-button>
      </div>
    </certificate-filter>
    <el-main>
      <el-table :data="list" border style="text-align: center">
        <el-table-column label="" prop="productionName"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <img :src="scope.row.productionImage[0].url" width="50" height="50">
          </template>
        </el-table-column>
        <el-table-column label="" prop="coachProfit"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.recommend | statusFilter}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.effective | statusFilter}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="shareNum"></el-table-column>
        <el-table-column label="" prop="buyNum"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{ scope.row.showRegion | showRegionFilter}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" @click="editProduction(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer>
      <pagination :total="total" :listQuery="listQuery" :GetList="GetRecommendProduction"></pagination>
    </el-footer>
    <el-dialog title="/" :visible.sync="visible" center>
      <el-form inline label-width="120px" label-position="right" :model="productionForm" ref="productionForm" :rules="productionRules">
        <el-form-item label="" prop="productionNo">
          <el-input v-model="productionForm.productionNo" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="productionName">
          <el-input v-model="productionForm.productionName" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="productionImage">
          <el-upload action="https://httpbin.org/post" list-type="picture-card" :file-list="productionForm.productionImage" :on-success="handleSuccess">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="coachProfit">
          <el-input v-model.number="productionForm.coachProfit" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="recommend">
          <el-radio-group v-model="productionForm.recommend">
            <el-radio label="true"></el-radio>
            <el-radio label="false"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="effective">
          <el-radio-group v-model="productionForm.effective">
            <el-radio label="true"></el-radio>
            <el-radio label="false"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="showRegion">
          <el-radio-group v-model="productionForm.showRegion">
            <el-radio label="all"></el-radio>
            <el-radio label="China"></el-radio>
            <el-radio label="abroad"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <tinymce :height="300" v-model="productionForm.detail"></tinymce>
        </el-form-item>
      </el-form>
      <span class="dialog-footer" slot="footer">
        <el-button type="primary" @click="submitForm"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { getRecommendProduction, addProduction } from 'api/operationManage'
  import tinymce from 'views/tinymce/tinymce'
  export default {
    name: 'recommendGoods',
    data () {
      return {
        list: null,
        total: 1,
        listQuery: {page: 1, productionName: null},
        visible: false,
        productionForm: {
          productionNo: '',
          productionName: '',
          productionImage: [],
          coachProfit: '',
          recommend: '',
          effective: '',
          showRegion: '',
          detail: '',
          id: (Math.random() * 100000000000).toFixed(0)
        },
        productionRules: {
          productionNo: [{required: true, trigger: 'blur', message: ''}],
          productionName: [{required: true, trigger: 'blur', message: ''}],
          productionImage: [{required: true, trigger: 'blur', message: ''}],
          coachProfit: [{required: true, trigger: 'blur, change', message: ''}, {type: 'number', message: ''}],
          recommend: [{required: true, trigger: 'blur, change', message: ''}],
          effective: [{required: true, trigger: 'blur, change', message: ''}],
          showRegion: [{required: true, trigger: 'blur, change', message: ''}],
          detail: [{required: true, trigger: 'blur', message: ''}]
        },
        isEdit: false
      }
    },
    created () { this.GetRecommendProduction() },
    methods: {
      GetRecommendProduction () {
        getRecommendProduction(this.listQuery).then(response => {
          this.list = response.data.recommendProduction
          this.total = response.data.total
        })
      },
      submitForm () {
        this.$refs.productionForm.validate(valid => {
          if (valid) {
            if (this.isEdit) {
              for (const [i, item] of this.list.entries()) {
                if (item.id === this.productionForm.id) {
                  this.list.splice(i, 1, this.productionForm)
                  this.$message.success('')
                  break
                }
              }
            } else {
              addProduction(this.productionForm).then(() => {
                this.$message.success('')
                this.list.unshift(this.productionForm)
              })
            }
            this.visible = false
          }
        })
      },
      editProduction (row) {
        this.isEdit = true
        this.visible = true
        this.productionForm = row
        this.productionForm.recommend = row.recommend + ''
        this.productionForm.effective = row.effective + ''
      },
      addProduction () {
        this.visible = true
        this.isEdit = false
        this.productionForm = {
          productionNo: '',
          productionName: '',
          productionImage: [],
          coachProfit: '',
          recommend: '',
          effective: '',
          showRegion: '',
          detail: ''
        }
        this.$nextTick(() => {
          this.$refs.productionForm.clearValidate()
        })
      },
      handleSuccess (response, file) {
        this.productionForm.productionImage.push({name: file.name, url: file.url})
      },
      acceptQuery (query) {
        this.listQuery = query.listQuery
      }
    },
    filters: {
      showRegionFilter (type) {
        const regionObj = {'all': '', 'China': '', 'abroad': ''}
        return regionObj[type]
      },
      statusFilter (val) {
        const statusObj = {'true': '', 'false': ''}
        return statusObj[val]
      }
    },
    components: { tinymce }
  }
</script>
<style lang="less">
.production-container .el-dialog {
  width: 80%;
  .el-form-item {
    display: block;
    &__content {
      width: 85%;
    }
  }
}
</style>



